import React, { Component } from 'react';
import $ from 'jquery';
import { findDOMNode } from 'react-dom';
import FormLayout from './FormLayout';

class ReactForm extends Component {
	state ={
		checkstate : false
	}

	handleSubmit = (e) => {
		e.preventDefault();

		var formData ={
			name: e.target.name.value,
			companyname: e.target.company_name.value,
			email: e.target.email.value,
			phone: e.target.phone_no.value,
			budget: e.target.budget_range.value,
			work: e.target.work_type.value,
			projectdetail: e.target.projectdetail.value,
			pricerange: e.target.pricerange.value,
			checkstate : this.state.checkstate	
		}
		console.log(formData);
		const el = findDOMNode(this.refs.object);

	    el.append("{ name: "+  formData.name +  
					",companyname: "+  formData.companyname +
					",email: "+    formData.email + 
					",phone: "+    formData.phone +
					",budget: "+    formData.budget +
					",work: "+    formData.work +
					",projectdetail: "+    formData.projectdetail +
					",pricerange: "+    formData.pricerange +
					",checkstate: "+    formData.checkstate +
	    "}");

	}
	
	checkboxhandleInputChange = (e) => {
		// this.setState({
		// 	isGoing: e.target.value
		// })
		const target = e.target;
		console.log(target.value);//on
		// const checkboxstateValue = target.type === 'checkbox' ? target.checked : !target.checked;
		// console.log(checkboxstateValue);
		if(target.type === 'checkbox'){
			var checkboxstateValue = target.checked
		}
		console.log(checkboxstateValue);//true/false

		this.setState({
			checkstate: checkboxstateValue
		});
	}

	// $("select").css('color','#aaa');
 //        $("select").change(function(){
 //            if($(this).val() === ""){
 //                $(this).color('color','#aaa')
 //            }else{
 //                $(this).css({
 //                    'color':'#fff',
 //                    'outline': 'none!important'
 //                });
 //            }
 //        });
 	setColor = (e) =>{
 		console.log("setColor fun hit");
 		console.log(this);
 	}
 
    render() {
   		// let getdataList = this.state.dataset.map((datalist)=>{
   		// 	console.log(datalist)
   		// 	return(
   		// 		<li key={datalist.counter}><span className="counterbg">{datalist.counter}</span> - {datalist.firstname} - {datalist.lastname} - {datalist.selectfruit} - {datalist.totalGuest}</li>
   		// 	);
   		// })
   		return(
	   		<div>
	 			<FormLayout submitform={this.handleSubmit} checkstate={this.checkboxhandleInputChange} setcolor={this.setColor}/>   		
	 			<div ref="object" className="json_result"></div>
	 		</div>
		)
   	}
}

export default ReactForm;


